import 'regenerator-runtime/runtime';
import React from 'react';

import './assets/global.css';
import ItemsList from './components/ItemsList';

import { EducationalText, SignInPrompt, SignOutButton } from './ui-components';


export default function App({ isSignedIn, itemsListed,contractId, wallet }) {
  const [valueFromBlockchain, setValueFromBlockchain] = React.useState();

  const [uiPleaseWait, setUiPleaseWait] = React.useState(true);

  const [items, setItems] = useState([]);
  // Get blockchian state once on component load
  React.useEffect(() => {
    getGreeting()
      .then(setValueFromBlockchain)
      .catch(alert)
      .finally(() => {
        setUiPleaseWait(false);
      });
    }
  , []);

  /// If user not signed-in with wallet - show prompt
  if (!isSignedIn) {
    // Sign-in flow will reload the page later
    return <SignInPrompt greeting={valueFromBlockchain} onClick={() => wallet.signIn()}/>;
  }

  function changeGreeting(e) {
    e.preventDefault();
    setUiPleaseWait(true);
    const { greetingInput } = e.target.elements;
    
    // use the wallet to send the greeting to the contract
    wallet.callMethod({ method: 'set_greeting', args: { message: greetingInput.value }, contractId })
      .then(async () => {return getGreeting();})
      .then(setValueFromBlockchain)
      .finally(() => {
        setUiPleaseWait(false);
      });
  }

  function getGreeting(){
    // use the wallet to query the contract's greeting
    return wallet.viewMethod({ method: 'get_greeting', contractId })
  }

  useEffect(() => {
    itemsListed.getItems().then(setItems);
  }, []);

  addItem = async (e) => {
    e.preventDefault();

    const { itemName, itemQuantitiy, itemPrice } = e.target.elements;



    await itemsListed.addItems(itemName.value, itemQuantitiy.value, itemPrice.value)
    const items = await itemsListed.getItems()

    setItems(items);
    itemName.value = '';
    itemQuantitiy.value = '0';
    itemPrice.value = 0;
    itemName.focus();
  };

  return (
    <>
      <SignOutButton accountId={wallet.accountId} onClick={() => wallet.signOut()}/>
      <main className={uiPleaseWait ? 'please-wait' : ''}>
        <h1>
          The contract says: <span className="greeting">{valueFromBlockchain}</span>
        </h1>
        <form onSubmit={changeGreeting} className="change">
          <label>Change greeting:</label>
          <div>
            <input
              autoComplete="off"
              defaultValue={valueFromBlockchain}
              id="greetingInput"
            />
            <button>
              <span>Save</span>
              <div className="loader"></div>
            </button>
          </div>
        </form>
        <form onSubmit={addItem}>
          <fieldset id="fieldset">
            {/* <p>Sign the guest book, { currentAccountId }!</p> */}
            <p className="highlight">
              <label htmlFor="message">Item Name:</label>
              <input
                autoComplete="off"
                autoFocus
                id="itemName"
                required
              />
            </p>
            <p >
              <label htmlFor="message">Item Quantity:</label>
              <input
                autoComplete="off"
                autoFocus
                id="itemQuantity"
                required
              />
            </p>
            <p>
              <label htmlFor="donation">Price:</label>
              <input
                autoComplete="off"
                defaultValue={'0'}
                id="itemPrice"
                min="0"
                step="0.1"
                type="number"
                required
              />
            </p>
            <button type="submit">
              save
            </button>
          </fieldset>
        </form>
        { !!items.length && <ItemsList items={items}/> }
        <EducationalText/>
      </main>
    </>
  );
}