import React from 'react'

function ItemsList({items}) {
  return (
    <div>

        <div>ItemsList</div>

        <div>
            <h2> Items Listed</h2>
            {items.map((item, i) =>
        // TODO: format as cards, add timestamp
                <p key={i} className={item.itemName}>

                <strong>{item.itemName}</strong>:<br/>
                {item.itemQuantity}
                {item.itemPrice}
                </p>
            )}
        </div>


    </div>
  )
}

export default ItemsList